import React, {Component} from 'react';
import logo from './logo.svg';
import auto_ru_logo from './auto_ru.svg';
import './App.css';
import listings from './Data.js';

class Listing extends React.Component {
  constructor(props) {
    super(props);
    this.data = listings[this.props.listID];
  }
  parsePrice = () => {
    let out = '';
    let i;
    let n = 0;
    for (i = this.data.price.toString().length - 1; i >= 0; i --) {
      out = this.data.price.toString()[i] + out;
      if (n === 2) {
        out = ' ' + out;
        n = 0;
      } else {
        n += 1;
      }
    }
    return out;
  };
  parseMileage = () => {
    let out = '';
    let i;
    let n = 0;
    for (i = this.data.mileage.toString().length - 1; i >= 0; i --) {
      out = this.data.mileage.toString()[i] + out;
      if (n === 2) {
        out = ' ' + out;
        n = 0;
      } else {
        n += 1;
      }
    }
    return out;
  };
  render() {
    return (
      <div className={'Listing'}>
        <h2>{this.data.make} <span style={{fontWeight: 'normal'}}>{this.data.model}</span></h2>
        <div style={{display: 'table', marginLeft: 'auto', marginRight: 'auto'}}>
          <div style={{display: 'table-cell', verticalAlign: 'middle'}}>
            <img className={'Listing'} src={this.data.image} alt={'car'}/>
          </div>
          <div style={{display: 'table-cell', verticalAlign: 'middle', width: '40vw'}}>
            <p><span style={{fontWeight: 'bold'}}>Year: </span>{this.data.year}</p>
            <p><span style={{fontWeight: 'bold'}}>Engine: </span>{this.data.engine}</p>
            <p><span style={{fontWeight: 'bold'}}>Mileage: </span>{this.parseMileage()} km</p>
          </div>
        </div>
        <p>
          <a href={this.data.url}>{this.parsePrice()} ₽ - <img className={'AutoRu'} alt={'auto.ru'} src={auto_ru_logo}/></a>
        </p>
      </div>
    );
  }
}

class Filter extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      make: 'Any',
      minYear: 1950,
      maxYear: 2020,
      minPrice: 0,
      maxPrice: 9500000,
    };
  }
  loadListings = () => {
    let lst = [];
    let i;
    for (i = 0; i < listings.length; i ++) {
      if ((this.state.make === 'Any' || this.state.make === listings[i].make)
          && (this.state.minYear <= listings[i].year)
          && (this.state.maxYear >= listings[i].year)
          && (this.state.minPrice <= listings[i].price)
          && (this.state.maxPrice >= listings[i].price)) {
        lst.push(<Listing key={i} listID={i}/>);
        console.log(i);
      }
    }
    return lst;
  };
  makeChoice = (event) => {
    this.setState({make: event.target.value});
  };
  minYearChoice = (event) => {
    this.setState({minYear: event.target.value});
  };
  maxYearChoice = (event) => {
    this.setState({maxYear: event.target.value});
  };
  minPriceChoice = (event) => {
    this.setState({minPrice: event.target.value});
  };
  maxPriceChoice = (event) => {
    this.setState({maxPrice: event.target.value});
  };
  render() {
    return (
        <div>
          <br/>
          <div>
            <div className={'filterSet'}>
              <label><span style={{fontWeight: 'bolder'}}>Make: </span></label>
              <select onChange={this.makeChoice} value={this.state.make}>
                <option value={'Any'}>Any</option>
                <option value={'BMW'}>BMW</option>
                <option value={'Mercedes'}>Mercedes</option>
              </select>
              <br className={'smartBreak'}/>
              <label><span style={{fontWeight: 'bolder'}}> Min Year: </span>{this.state.minYear}</label>
              <input className={'slider'} onChange={this.minYearChoice} type={'range'} min={'1950'} max={this.state.maxYear} value={this.state.minYear}/>
              <br className={'smartBreak'}/>
              <label><span style={{fontWeight: 'bolder'}}> Max Year: </span>{this.state.maxYear}</label>
              <input className={'slider'} onChange={this.maxYearChoice} type={'range'} min={this.state.minYear} max={'2020'} value={this.state.maxYear}/>
            </div>
            <div className={'filterSet'}>
              <label><span style={{fontWeight: 'bolder'}}>Min Price: </span>{(this.state.minPrice/1000000).toFixed(1)} M ₽ </label>
              <input className={'slider'} onChange={this.minPriceChoice} type={'range'} min={'0'} max={this.state.maxPrice} value={this.state.minPrice} step={'500000'}/>
              <br className={'smartBreak'}/>
              <label><span style={{fontWeight: 'bolder'}}> Max Price: </span>{(this.state.maxPrice/1000000).toFixed(1)} M ₽ </label>
              <input className={'slider'} onChange={this.maxPriceChoice} type={'range'} min={this.state.minPrice} max={'9500000'} value={this.state.maxPrice} step={'500000'}/>
            </div>
          </div>
          {this.loadListings()}
        </div>
    )
  }
}

function App() {
  return (
    <div className="App">
      <header className="App-header">
        <Filter/>
      </header>
    </div>
  );
}

export default App;
